<template>
  <div>
    <div v-if="!showEdit && !showAdd" class="container">
      <div class="header">
        <div class="title">激活码列表</div>
        <el-button style="margin-left: 30px" @click="showAdd=!showAdd">生成激活码</el-button>
      </div>
      <ceres-table
        :pagination="pagination"
        :table-column="tableColumn"
        :table-data="tableData"
        :operate="operate"
        @handleEdit="handleEdit"
        @handleDelete="handleDelete"
        @currentChange = "currentChange"
      />
    </div>

    <key-add v-if="showAdd" @hanleHide="handleHideAdd"/>

  </div>
</template>

<script type="text/ecmascript-6">
import cdKeyModel from '@/models/cdKey'
import KeyAdd from './KeyAdd'

export default {
  name: 'cdkey',
  components: {
    KeyAdd
  },
  data() {
    return {
      tableColumn: [
        { prop: 'cdkey', label: '激活码' },
        { prop: 'company_name', label: '公司' },
        { prop: 'project_name', label: '项目' },
        { prop: 'expired_time', label: '时间' },
      ],
      tableData: [],
      operate: [
        { name: '激活/失效', func: 'handleEdit', type: 'primary' },
        { name: '删除', func: 'handleDelete', type: 'danger' },
      ],
      showEdit: false,
      showAdd: false,
      pagination: {
        pageSize: 0,
        pageTotal: 0
      },
      editID: ''
    }
  },
  computed: {},
  created() { },
  mounted() {
    this.getKeyList(1, 10)
  },
  methods: {
    async getKeyList(page, size) {
      const res = await cdKeyModel.getList(size, page)
      this.tableData = [...res.items]
      this.pagination.pageTotal = res.total
      this.pagination.pageSize = res.size
    },
    handleEdit(val) {
      this.$confirm(`此操作将${val.row.state ? '失效' : '激活'}激活码, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await cdKeyModel.updateOne(val.row.cdkey, val.row.state ? 0 : 1)
        this.$message.success(`激活码${val.row.state ? '失效' : '激活'}成功`)
        this.getKeyList(1, 10)
      }).catch(() => {
      })
    },
    async handleDelete(val) {
      await cdKeyModel.deleteOne(val.row.cdkey)
      this.$message.success('激活码删除成功')
      this.getKeyList(1, 10)
    },
    handleHideAdd() {
      this.showAdd = false
      this.getKeyList(1, 10)
    },
    handleHideEdit() {
      this.showEdit = false
      this.getKeyList(1, 10)
    },
    // 切换当前页
    currentChange(page) {
      this.getKeyList(page, 10)
    }
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .container {
    .header {
      display flex
      align-items center
      .title {
        height 59px
        line-height 59px
        font-size 16px
        font-weight 500
      }
    }
  }
</style>
